import React from 'react';
import {
    Checkbox,
    FormControlLabel,
    List,
    ListItem,
    ListItemIcon,
    ListItemText
} from "@mui/material";
import {
    AdhdIcon,
    AutismIcon, BehaviorIcon,
    BlindIcon, CommunicationIcon, DeafIcon,
    DevelopmentIcon, DiseaseIcon, IntellectualIcon, WheelchairIcon

} from "../../../assets/icons/PreferencesIcons";
import {Blind} from "@mui/icons-material";


function SpecialNeedsList(props) {
    const info = props.info
    const allergensList = [
        {label : "Trastornos graves del desarrollo ", info: "limitacin en el funcionamiento cognitivo o adaptativo que se inicia en la infancia y produce alteraciones de conducta en diversas reas de la vida diaria, como el hogar o la escuela.", icon: DevelopmentIcon() },
        {label : "Discapacidad visual", info: "discapacidad sensorial que consiste en la prdida total o parcial del sentido de la vista. ", icon: BlindIcon() },
        {label : "Discapacidad intelectual", info: " alteracin en el desarrollo del ser humano caracterizada por limitaciones significativas tanto en el funcionamiento intelectual como en las conductas adaptativas y que se evidencia antes de los 18 aos de edad. ", icon: IntellectualIcon() },
        {label : "Discapacidad auditiva", info: "dificultad o la imposibilidad de usar el sentido del odo debido a una prdida de la capacidad auditiva parcial (hipoacusia) o total (cofosis), y unilateral o bilateral. ", icon: DeafIcon() },
        {label : "Trastornos de la comunicacin", info: "Trastorno del discurso y del lenguaje que se refieren a problemas en la comunicacin y reas relacionadas, como la funcin motora oral. Los retrasos y trastornos pueden ir desde la sustitucin de sonidos simples a la incapacidad de comprender o utilizar su lengua materna.", icon: CommunicationIcon() },
        {label : "Discapacidad fsica", info: "deficiencia que provoca en el individuo que la padece alguna disfuncin en el aparato locomotor.", icon: WheelchairIcon()},
        {label : "Trastornos del Espectro Autista", info: "Abarcan un amplio espectro de trastornos que, en su manifestacin fenotpica, se caracterizan por deficiencias persistentes en la comunicacin social y en la interaccin social en diversos contextos, unidas a patrones restrictivos y repetitivos de comportamiento, intereses o actividades.", icon: AutismIcon() },
        {label : "Trastornos graves de conducta", info: "Es una serie de problemas conductuales y emocionales que se presentan en nios y adolescentes. Los problemas pueden involucrar comportamiento desafiante o impulsivo, consumo de drogas o actividad delictiva.", icon: BehaviorIcon() },
        {label : "Trastorno por Dficit de Atencin con/sin Hiperactividad", info: "trastorno crnico del desarrollo neuropsiquitrico que afecta el desarrollo y funcionamiento de las funciones ejecutivas.", icon: AdhdIcon() },
        {label : "Enfermedades raras y crnicas", info: "Enfermedades que afectan a una proporcin reducida de la poblacin.", icon: DiseaseIcon() },
    ];

    return (
        <>
            <List >
                {
                    allergensList.map( (item) =>
                        {
                            return (
                                <ListItem>
                                    <ListItemIcon>
                                        {item.icon}
                                    </ListItemIcon>

                                    <ListItemText
                                        primary={
                                            <FormControlLabel
                                                labelPlacement="start"
                                                label={item.label}
                                                control={<Checkbox/>}
                                                  sx={{
                                                      width: '230px',
                                                      display: 'flex',
                                                      justifyContent: 'space-between',
                                                      alignItems: 'center'
                                                  }}
                                            />}
                                        secondary={ info ? item.info : null}
                                    />
                                </ListItem>
                            )
                        }
                    )
                }
            </List>
        </>
    );
}

export default SpecialNeedsList;